import {Component} from '@angular/core';
import {Router} from '@angular/router';
import {ActivatedRoute} from '@angular/router';
import {TripReport} from '@models/reports/tripReport.model';
import {TripReportModalPage} from '@app/pages/trip-report-modal/trip-report-modal.page';
import {ModalController} from '@ionic/angular';

@Component({
  selector: 'app-trips-report',
  templateUrl: './trips-report.page.html',
  styleUrls: ['./trips-report.page.scss'],
})
export class TripsReportPage {

  public report: TripReport;

  constructor(private modalCtrl: ModalController, private route: ActivatedRoute, private router: Router) {
    this.route.queryParams.subscribe(params => {
      if (this.router.getCurrentNavigation().extras.state) {
        this.report = this.router.getCurrentNavigation().extras.state.report;
      }
    });
  }

  async onShowReport(reportType: string) {
    const modalPage = await this.modalCtrl.create({
      component: TripReportModalPage,
      componentProps: {report: this.report, reportType}
    },);
    await modalPage.present();
  }

}
